export default "<template>\n  <div\n    class=\"bg-color\"\n  >\n    <div\n      v-for=\"color in colors\"\n      :key=\"color\"\n      class=\"bg-color__color\"\n    >\n      <div\n        v-for=\"i in 2\"\n        :key=\"i\"\n        :class=\"`has-bg-color-${color}-lighten-${3 - i}`\"\n      >\n        has-bg-color-{{ color }}-lighten-{{ 3 - i }}\n      </div>\n      <div\n        :class=\"`has-bg-color-${color}`\"\n      >\n        has-bg-color-{{ color }}\n      </div>\n      <div\n        v-for=\"i in 2\"\n        :key=\"i\"\n        :class=\"`has-bg-color-${color}-darken-${i}`\"\n      >\n        has-bg-color-{{ color }}-darken-{{ i }}\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { colors } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'CommonClassesBgColor',\n  data () {\n    return {\n      colors\n    };\n  }\n};\n</script>\n\n<style>\n.bg-color {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n}\n.bg-color__color {\n  margin: 0 20px 20px 0;\n}\n</style>\n";